
import { defineComponent } from "vue";
import IgButton from "@/components/baseUI/IgButton.vue";
import SelectBankName from "./SelectBankName.vue";
export default defineComponent({
  name: "WithdrawForm",
  components: {
    IgButton,
    SelectBankName,
  },
  props: {
    Icon: {
      type: String,
      required: true,
    },
    nextpaymentStep: {
      type: Function,
      required: true,
    },

    amount: {
      type: String,
      required: true,
    },

    addAmount: {
      type: String,
      required: true,
    },

    addNumber: {
      type: String,
      required: true,
    },

    bankAccountNumber: {
      type: String,
      required: true,
    },
    bankName: {
      type: String,
      required: true,
    },
    bankHolder: {
      type: String,
      required: true,
    },
    Proceed: {
      type: String,
      required: true,
    },
    addName: {
      type: String,
      required: true,
    },
  },
});
