import { Menu, MenuButton, MenuItems, MenuItem } from "@headlessui/vue";
import IgButton from "@/components/baseUI/IgButton.vue";

export default defineComponent({
  data: () => ({}),
  components: {
    Carousel,
    Slide,
    Menu,
    MenuButton,
    MenuItems,
    MenuItem,
    IgButton,
  },
  computed: {
    ...mapState(["WalletOpen"]),
  },
  methods: {
    closeWalletStatus() {
      this.WalletStatus = false;
    },
    ...mapMutations([
      "TOGGLE_WALLET",
      "TOGGLE_CHAT",
      "TOGGLE_NOTIFICATION_STATUS",
    ]),
    OpenWallet() {
      this.TOGGLE_WALLET();
    },
    toggleChat() {
      this.TOGGLE_CHAT();
      console.log("now working");
    },
    showNotification() {
      this.TOGGLE_NOTIFICATION_STATUS();
    },
    ...mapActions(["destroyToken"]),

    logOut() {
      this.destroyToken().then(() => {
        // make the page reload
        location.reload();
        this.$router.push("/");
      });
    },
  },
});
</script>
