
import { defineComponent } from "vue";
import { mapMutations, mapState } from "vuex";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";

export default defineComponent({
  name: "AllTournaments",
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
  },
  computed: {
    ...mapState(["AllTournaments"]),
  },
  methods: {
    ...mapMutations(["ALL_TOURNAMENTS"]),
    toggleAllTournaments() {
      this.ALL_TOURNAMENTS();
    },
  },
});
