
import { mapMutations, mapState } from "vuex";
import { defineComponent } from "vue";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";

export default defineComponent({
  name: "igDialog",
  props: {
    ModalOpen: {
      type: Boolean,
    },
    ModalName: {
      type: String,
    },
    backgroundColor: {
      type: String,
    },
    fontBold: {
      type: String,
    },
  },
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
  },
  methods: {
    ...mapMutations(["TOGGLE_LOGIN_MODAL"]),
    openModal() {
      this.TOGGLE_LOGIN_MODAL();
    },
  },
  computed: {
    ...mapState(["loginModal"]),
  },
});
