
import { defineComponent } from "vue";
import { mapMutations, mapState } from "vuex";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";

export default defineComponent({
  name: "AllGames",
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
  },
  computed: {
    ...mapState(["AllGamesModal"]),
  },
  methods: {
    ...mapMutations(["TOGGLE_ALL_GAMES"]),
    toggleAllGames() {
      this.TOGGLE_ALL_GAMES();
    },
  },
});
