
import { defineComponent } from "vue";
import IgLinks from "./IgLinks.vue";
import IgLogo from "../baseUI/IgLogo.vue";
import IgSearch from "../baseUI/IgSearch.vue";
import IgLogin from "../baseUI/Login.vue";
import IgSignup from "../baseUI/Signup.vue";
import MobileNav from "./MobileNav.vue";
import AddScreens from "../add-screens/AddScreens.vue";
import LoginModal from "../baseUI/LoginModal.vue";
import SignupModal from "../baseUI/SignupModal.vue";

import { mapState, mapMutations, mapGetters } from "vuex";
export default defineComponent({
  components: {
    IgLinks,
    IgSearch,
    IgLogin,
    IgSignup,
    IgLogo,
    MobileNav,
    LoginModal,
    AddScreens,
    SignupModal,
  },
  data: () => {
    return {
      LoginIN: true,
      SignUP: false,
      LoginInSignUpModal: false,
      SignUpInSignUpModal: true,
    };
  },
  methods: {
    showSignUP() {
      this.LoginIN = false;
      this.SignUP = true;
      this.SignUpInSignUpModal = true;
      this.LoginInSignUpModal = false;
    },
    ShowLogin() {
      this.LoginIN = true;
      this.SignUP = false;
      this.LoginInSignUpModal = true;
      this.SignUpInSignUpModal = false;
    },
    ...mapMutations(["TOGGLE_ADD_SCREENS"]),
    toggleAddscreens() {
      this.TOGGLE_ADD_SCREENS();
    },
  },
  computed: {
    ...mapState(["ADDNEWSCREENS", "isLoggin", "UserLogin"]),
    ...mapGetters(["loggedIn"]),
    // loggedIn() {
    //   return this.$store.getters.loggedIn;
    // },
  },
});
