<template>
  <div class="flex justify-center items-center flex-col pt-9 relative">
    <span role="img">
      <img :src="Icon" alt="Success Message" />
    </span>
    <div class="flex justify-center flex-col m-auto items-center mt-7">
      <h3 class="text-2xl text-white font-bold">
        {{ $t("wallet." + yourTopUpSuccess + "") }}
      </h3>
      <p class="text-base text-gray-200 text-center mb-8">
        {{ $t("wallet." + thankYouFortopUp + "") }}
      </p>
      <ig-button
        @click="OkCloseModal"
        buttonName="ok"
        backgroundColor="ig-blue"
        textColor="ig-text-white"
      >
      </ig-button>
      <span
        class="
          cursor-pointer
          absolute
          top-2
          right-4
          hover:opacity-70
          transition
          duration-500
          ease-in-out
        "
        @click="OkCloseModal"
      >
        <img :src="closeIcon" alt="Close Button" />
      </span>
    </div>
  </div>
</template>

<script lang="ts">
import { mapMutations } from "vuex";
import { defineComponent } from "vue";
import IgButton from "../../baseUI/IgButton.vue";
export default defineComponent({
  name: "SuccessMessage",
  components: {
    IgButton,
  },
  props: {
    Icon: {
      type: String,
      required: true,
    },
    yourTopUpSuccess: {
      type: String,
      required: true,
    },
    thankYouFortopUp: {
      type: String,
      required: true,
    },
    ok: {
      type: String,
      required: true,
    },
    closeIcon: {
      type: String,
      required: true,
    },
  },
  methods: {
    ...mapMutations(["TOGGLE_MODAL"]),
    OkCloseModal() {
      this.TOGGLE_MODAL();
    },
  },
});
</script>
