
import { mapState } from "vuex";
import baseWithdraw from "@/components/baseUI/WithdrawModal.vue";
import { defineComponent } from "vue";
import WithdrawHeader from "./WithdrawHeader.vue";
import WithdrawalSuccess from "./WithdrawalSuccess.vue";
import WithdrawForm from "./WithdrawForm.vue";

export default defineComponent({
  name: "Withdraw",
  components: {
    baseWithdraw,
    WithdrawHeader,
    WithdrawalSuccess,
    WithdrawForm,
  },
  data: () => ({
    withdrawStep: 1,
    length: 2,
  }),
  computed: {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    ShowStpes(): any {
      return (this.withdrawStep / this.length) * 100;
    },
    ...mapState(["WithdrawOpen"]),
  },
  methods: {
    nextpaymentStep() {
      this.withdrawStep++;
    },
  },
});
