
import { mapMutations, mapState } from "vuex";
import { defineComponent, ref } from "vue";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";

import {
  Listbox,
  ListboxButton,
  ListboxOptions,
  ListboxOption,
} from "@headlessui/vue";
export default defineComponent({
  name: "Team",
  setup() {
    const people = [
      { name: "Select" },
      { name: "Arlene Mccoy" },
      { name: "Devon Webb" },
      { name: "Tom Cook" },
      { name: "Tanya Fox" },
      { name: "Hellen Schmidt" },
    ];
    const selectedPerson = ref(people[0]);

    return {
      people,
      selectedPerson,
    };
  },
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
    Listbox,
    ListboxButton,
    ListboxOptions,
    ListboxOption,
  },
  computed: {
    ...mapState(["TeamModal"]),
  },
  methods: {
    ...mapMutations(["TOGGLE_TEAM_MODAL"]),
    toggleTeam() {
      this.TOGGLE_TEAM_MODAL();
    },
  },
});
