
import { Stepper } from "vuejs-stepper";

import { mapMutations, mapState } from "vuex";
import { defineComponent, ref } from "vue";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";

import {
  Listbox,
  ListboxButton,
  ListboxOptions,
  ListboxOption,
} from "@headlessui/vue";

export default defineComponent({
  name: "CoachingService",
  data: () => ({
    stepperOptions: {
      headers: [
        { title: "Select game" },
        { title: "About" },
        { title: "Packages" },
      ],

      nextText: "Next",
    },
  }),
  setup() {
    const people = [
      { name: "Select" },
      { name: "Arlene Mccoy" },
      { name: "Devon Webb" },
      { name: "Tom Cook" },
      { name: "Tanya Fox" },
      { name: "Hellen Schmidt" },
    ];
    const selectedPerson = ref(people[0]);

    return {
      people,
      selectedPerson,
    };
  },
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
    Stepper,
    Listbox,
    ListboxButton,
    ListboxOptions,
    ListboxOption,
  },
  computed: {
    ...mapState(["CoachingService"]),
  },
  methods: {
    ...mapMutations(["TOGGLE_CoachingService_MODAL"]),
    toggleCoachingService() {
      this.TOGGLE_CoachingService_MODAL();
    },
  },
});
