
import { mapMutations } from "vuex";
import { defineComponent } from "vue";
import IgButton from "../../baseUI/IgButton.vue";
export default defineComponent({
  name: "SuccessMessage",
  components: {
    IgButton,
  },
  props: {
    Icon: {
      type: String,
      required: true,
    },
    yourTopUpSuccess: {
      type: String,
      required: true,
    },
    thankYouFortopUp: {
      type: String,
      required: true,
    },
    ok: {
      type: String,
      required: true,
    },
    closeIcon: {
      type: String,
      required: true,
    },
  },
  methods: {
    ...mapMutations(["TOGGLE_MODAL"]),
    OkCloseModal() {
      this.TOGGLE_MODAL();
    },
  },
});
