
import { defineComponent } from "vue";
import YourWallet from "./YourWallet.vue";
import LatestTransactions from "./LatestTransactions.vue";
import { mapMutations } from "vuex";
export default defineComponent({
  components: {
    YourWallet,
    LatestTransactions,
  },
  name: "Wallets",
  data: () => {
    return {};
  },
  props: {
    WalletStatus: {
      type: Boolean,
      required: true,
    },
    Title: {
      type: String,
      required: true,
    },
  },
  methods: {
    ...mapMutations(["TOGGLE_WALLET"]),
    closeWalletStatus() {
      this.TOGGLE_WALLET();
    },
  },
