
import { defineComponent } from "vue";
import { mapMutations, mapState } from "vuex";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";

export default defineComponent({
  name: "AllRecommendedCoaching",
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
  },
  computed: {
    ...mapState(["AllRecommendedCoaching"]),
  },
  methods: {
    ...mapMutations(["ALL_RECOMMEND_COACHING"]),
    toggleAllRecoCoaching() {
      this.ALL_RECOMMEND_COACHING();
    },
  },
});
