import axios from "axios";
import { createStore } from "vuex";
export default createStore({
  state: {
    imageUrl: "http://igame-env.eba-vdb4aabf.us-west-2.elasticbeanstalk.com",
    apiUrl: "http://igame-env.eba-vdb4aabf.us-west-2.elasticbeanstalk.com/api",
    isLoggin: true,
    // rightBar
    // isLoggin: false,
    // useIsLoging
    // UserLogin: false,
    loginModal: false,
    signupModal: false,
    ModalOpen: false,
    AllGamesModal: false,
    AllRecommendedCoaching: false,
    AllTournaments: false,
    AllFoundGames: false,
    AllFoundSpaces: false,
    AllFoundPlayers: false,
    AllFoundTeams: false,
    WithdrawOpen: false,
    WalletOpen: false,
    chatStatus: false,
    messagesStatus: false,
    groupMessageStatus: false,
    MembersStatus: false,
    teamChatStatus: false,
    gameChatStatus: false,
    tournamentChatStatus: false,
    ADDPOST: false,
    addNewChatStatus: false,
    ADDNEWSCREENS: false,
    TeamModal: false,
    SpaceModal: false,
    SpacePost: false,
    CoachingService: false,
    TournamentModal: false,
    // AuthLoding: false,
    NotificationStatus: false,
    // auth: {}
    token: localStorage.getItem("access_token") || null,
  },
  mutations: {
    // toggle loginModal
    TOGGLE_LOGIN_MODAL(state) {
      state.loginModal = !state.loginModal;
    },
    CLOSE_LOGIN_MODAL(state) {
      state.loginModal = false;
    },
    CLOSE_SIGNUP_MODAL(state) {
      state.signupModal = false;
    },
    TOGGLE_SIGNUP_MODAL(state) {
      state.signupModal = !state.signupModal;
    },
    // top up modal
    TOGGLE_MODAL(state) {
      state.ModalOpen = !state.ModalOpen;
    },
    // withdraw modal
    TOGGLE_Withdraw(state) {
      state.WithdrawOpen = !state.WithdrawOpen;
    },

    // toggle notification status
    TOGGLE_NOTIFICATION_STATUS(state) {
      state.NotificationStatus = !state.NotificationStatus;
    },
    // toggel wallet
    TOGGLE_WALLET(state) {
      state.WalletOpen = !state.WalletOpen;
    },

    //toggle chat
    TOGGLE_CHAT(state) {
      state.chatStatus = !state.chatStatus;
    },
    // toggle chatMessage
    TOGGLE_CHAT_MESSAGE(state) {
      state.messagesStatus = !state.messagesStatus;
    },

    // toggle group message

    TOGGLE_GROUP_MESSAGE(state) {
      state.groupMessageStatus = !state.groupMessageStatus;
    },
    // toggle groupe members
    TOGGLE_GROUP_MEMBERS(state) {
      state.MembersStatus = !state.MembersStatus;
    },

    // toggle team chat

    TOGGLE_TEAM_CHAT(state) {
      state.teamChatStatus = !state.teamChatStatus;
    },
    // toggle game chat
    TOGGLE_GAME_CHAT(state) {
      state.gameChatStatus = !state.gameChatStatus;
    },

    // tournament chat
    TOGGLE_TOURNAMENT_CHAT(state) {
      state.tournamentChatStatus = !state.tournamentChatStatus;
    },
    TOGGLE_ADD_POST(state) {
      state.ADDPOST = !state.ADDPOST;
    },

    // toggle add new chat
    TOGGLE_ADD_NEW_CAHT(state) {
      state.addNewChatStatus = !state.addNewChatStatus;
    },
    TOGGLE_ADD_SCREENS(state) {
      state.ADDNEWSCREENS = !state.ADDNEWSCREENS;
    },
    TOGGLE_TEAM_MODAL(state) {
      state.TeamModal = !state.TeamModal;
    },
    TOGGLE_Space_MODAL(state) {
      state.SpaceModal = !state.SpaceModal;
    },
    TOGGLE_SpacePost_MODAL(state) {
      state.SpacePost = !state.SpacePost;
    },
    TOGGLE_CoachingService_MODAL(state) {
      state.CoachingService = !state.CoachingService;
    },
    TOGGLE_TournamentModal_MODAL(state) {
      state.TournamentModal = !state.TournamentModal;
    },

    TOGGLE_ALL_GAMES(state) {
      state.AllGamesModal = !state.AllGamesModal;
    },
    ALL_RECOMMEND_COACHING(state) {
      state.AllRecommendedCoaching = !state.AllRecommendedCoaching;
    },
    ALL_TOURNAMENTS(state) {
      state.AllTournaments = !state.AllTournaments;
    },
    ALL_FOUND_GAMES(state) {
      state.AllFoundGames = !state.AllFoundGames;
    },
    ALL_FOUND_SPACES(state) {
      state.AllFoundSpaces = !state.AllFoundSpaces;
    },

    ALL_FOUND_PLAYERS(state) {
      state.AllFoundPlayers = !state.AllFoundPlayers;
    },

    ALL_FOUND_TEAMS(state) {
      state.AllFoundTeams = !state.AllFoundTeams;
    },
    // Login
    retrieveToken(state, token) {
      state.token = token;
    },

    //Logout
    destroyToken(state) {
      state.loginModal = false;
      state.signupModal = false;
      // state.token = null;
    },
  },
  actions: {
    // Logout
    destroyToken(context) {
      // if (context.getters.loggedIn) {
      //   return new Promise((resolve, reject) => {
      //     axios.post("logout/").then(res => {
      //       localStorage.removeItem("access_token")
      //       context.commit("destroyToken")
      //       resolve(res)
      //     }).catch(err => {
      //       localStorage.removeItem("access_token")
      //       context.commit("destroyToken")
      //       reject(err)
      //     })
      //   })
      // }
      if (context.getters.loggedIn) {
        localStorage.removeItem("access_token");
        context.commit("destroyToken");
      }
    },

    // Register
    UserRegister(context, credentials) {
      return new Promise((resolve, reject) => {
        axios
          .post("signup/", credentials)
          .then((res) => {
            resolve(res);
          })
          .catch((err) => {
            reject(err);
          });
      });
    },

    // Login
    retrieveToken(context, credentials) {
      // axios.defaults.headers.common['Authorization'] = 'Bearer' + context.state.token
      return new Promise((resolve, reject) => {
        axios
          .post("login/", credentials)
          .then((res) => {
            const token = res.data.access;
            localStorage.setItem("access_token", token);
            // localStorage.setItem('auth', JSON.stringify(token))
            context.commit("retrieveToken", token);
            // this.state.auth = res.data;
            resolve(res);

            /// test
            // const AuthIsLoadin = this.state.AuthLoding;

            // this.state.AuthLoding = true;

            // setTimeout(() => {
            //   this.state.AuthLoding = false;

            //     console.log('working now?')
            // }, 4000);
          })
          .catch((err) => {
            console.log(err);
            reject(err);
          });
      });
    },
  },
  modules: {},
  getters: {
    loggedIn(state) {
      return state.token != null;
    },
  },
});
