<template>
  <div class="w-full">
    <div class="w-full max-w-md mx-auto">
      <RadioGroup v-model="selected">
        <div class="flex justify-between">
          <RadioGroupOption
            as="template"
            v-for="plan in plans"
            :key="plan.name"
            :value="plan"
            v-slot="{ active }"
          >
            <div
              :class="[active ? 'bg-white' : 'bg-gray-300']"
              class="
                relative
                rounded-lg
                shadow-md
                cursor-pointer
                focus:outline-none
                flex
                justify-center
              "
              style="width: 140px; height: 30px; margin-right: 15px"
            >
              <div class="flex items-center justify-between">
                <div class="flex items-center">
                  <div class="text-sm flex justify-center items-center">
                    <RadioGroupLabel
                      as="p"
                      :class="active ? 'text-black' : 'text-white'"
                      class="font-bold text-sm"
                    >
                      {{ plan.name }}
                    </RadioGroupLabel>
                  </div>
                </div>
              </div>
            </div>
          </RadioGroupOption>
        </div>
      </RadioGroup>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import { RadioGroup, RadioGroupLabel, RadioGroupOption } from "@headlessui/vue";

const plans = [
  {
    name: "Subscriber only",
  },
  {
    name: "Pay to join",
  },
  {
    name: "Free to join",
  },
];

export default defineComponent({
  components: {
    RadioGroup,
    RadioGroupLabel,
    RadioGroupOption,
  },

  setup() {
    const selected = ref(plans[0]);

    return { selected, plans };
  },
});
</script>
