
import TypeMessage from "../TypeMessage.vue";
import { defineComponent } from "vue";
import { mapMutations } from "vuex";

export default defineComponent({
  name: "startTournamentChat",
  components: { TypeMessage },
  methods: {
    ...mapMutations(["TOGGLE_TOURNAMENT_CHAT"]),
    hideTournamentChat() {
      this.TOGGLE_TOURNAMENT_CHAT();
    },
  },
});
