<template>
  <base-dialog
    :ModalOpen="ModalOpen"
    ModalName="top_up"
    backgroundColor="ig-white"
    fontBold="ig-bold"
  >
    <div class="flex-col m-auto">
      <div class="bg-gray-400 p-30 rounded-xlarge" style="width: 540px">
        <div class="flex flex-col relative" style="z-index: 4000">
          <div v-if="paymentStep === 1">
            <card-header
              Title="top_up_wallet"
              SelectMethod="select_on_method"
              :closeIcon="require('@/assets/images/close.svg')"
            ></card-header>
            <!-- // here user will choose which payemnt method will choose,, such as visa or credit card or other payment -->
            <choose-payment-method
              CreditCard="credit_card"
              DebitCard="debit_card"
              BankTransfer="bank_transfer"
              OtherPayment="other_payment"
              :nextpaymentStep="nextpaymentStep"
            ></choose-payment-method>
          </div>
          <!-- this is next step which user will fill info for credit card -->
          <info-credit-card
            v-if="paymentStep === 2"
            :nextpaymentStep="nextpaymentStep"
            creditCard="credit_card"
            amount="amount"
            addAmount="add_amount"
            creditCardNumber="credit_card_number"
            expiryDate="expiry_date"
            CCV="cvv"
            Confirm="confirm"
          >
            <!-- Card Header  -->
            <fill-info-header
              :backIcon="require('@/assets/images/wallet/back-arrow.svg')"
              :previouspaymentStep="previouspaymentStep"
              topUpWithCreditCard="top_up_with_credit_card"
              provideCreditInfo="provide_credit_info"
            >
            </fill-info-header>
            <!-- Badge for card  -->
            <credit-card-badge
              CreditName="credit_card"
              :Icon="require('@/assets/images/wallet/visa.svg')"
            ></credit-card-badge>
            <!-- From Inputs -->
            <form-inputs
              amount="amount"
              addAmount="add_amount"
              creditCardNumber="credit_card_number"
              addNumber="add_number"
              expireDate="expiry_date"
              addExpireDate="add_expiry_date"
              CVV="cvv"
              Confirm="confirm"
              :nextpaymentStep="nextpaymentStep"
              :Icon="require('@/assets/images/wallet/dollar-sign.svg')"
            ></form-inputs>
          </info-credit-card>
          <!-- this is success message which will show for the user after he or she fill info -->
          <your-top-up-success
            v-if="paymentStep == 3"
            :Icon="require('@/assets/images/wallet/fill-success-img.svg')"
            yourTopUpSuccess="your_top_up_success"
            thankYouFortopUp="thank_you_for_top_up"
            ok="ok"
            :closeIcon="require('@/assets/images/close.svg')"
          ></your-top-up-success>
        </div>
      </div>
    </div>
  </base-dialog>
</template>

<script lang="ts">
import BaseDialog from "@/components/baseUI/IgDialog.vue";
import { defineComponent } from "vue";
import CardHeader from "../screens/CardHeader.vue";
import ChoosePaymentMethod from "../screens/ChoosePaymentMethod.vue";
import InfoCreditCard from "../screens/cards/InfoForCreditCard.vue";
import yourTopUpSuccess from "../screens/Success.vue";
import FillInfoHeader from "../screens/FillInfoHeader.vue";
import FormInputs from "../screens/FormInputs.vue";
import CreditCardBadge from "../screens/CreditCardBadge.vue";
import { mapState } from "vuex";
export default defineComponent({
  name: "TopUpWallet",
  components: {
    BaseDialog,
    CardHeader,
    ChoosePaymentMethod,
    InfoCreditCard,
    yourTopUpSuccess,
    FillInfoHeader,
    FormInputs,
    CreditCardBadge,
  },
  data: () => ({
    paymentStep: 1,
    length: 4,
  }),
  computed: {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    ShowStpes(): any {
      return (this.paymentStep / this.length) * 100;
    },
    ...mapState(["ModalOpen"]),
  },
  methods: {
    nextpaymentStep() {
      this.paymentStep++;
      console.log("next step working ?");
    },
    previouspaymentStep() {
      this.paymentStep--;
      console.log("previous step wrokign ?");
    },
  },
});
</script>
