<template>
  <div class="flex justify-between items-center">
    <div class="mb-2">
      <h3 class="text-2xl text-white">
        {{ $t("wallet." + Title + "") }}
      </h3>
      <p class="text-base text-gray-200 font-normal">
        {{ $t("wallet." + SelectMethod + "") }}
      </p>
    </div>
    <button
      role="img"
      type="button"
      class="hover:opacity-50 transition duration-500 ease-in-out"
      @click="closeModal"
    >
      <img :src="closeIcon" />
    </button>
  </div>
</template>

<script lang="ts">
import { mapMutations } from "vuex";
import { defineComponent } from "vue";

export default defineComponent({
  methods: {
    ...mapMutations(["TOGGLE_MODAL"]),
    closeModal() {
      this.TOGGLE_MODAL();
    },
  },
  props: {
    Title: {
      type: String,
      required: true,
    },
    SelectMethod: {
      type: String,
      required: true,
    },
    closeIcon: {
      type: String,
      required: true,
    },
  },
});
</script>
