
import { mapMutations, mapState, mapActions } from "vuex";
import { defineComponent } from "vue";
import IgButton from "./IgButton.vue";
import Seperator from "../baseUI/Seperator.vue";
// import IgInput from "../baseUI/IgInput.vue";
// import PasswordContainer from "./PasswordContainer.vue";
import IgLogo from "./IgLogo.vue";
import LoadingAuth from "../baseUI/LoadingAuth.vue";
import { Form, Field, ErrorMessage } from "vee-validate";
import * as yup from "yup";
// import { mapActions } from "vuex";
// import axios from "axios";

export default defineComponent({
  name: "Signup",
  data() {
    const schema = yup.object({
      email: yup.string().required().email(),
      password: yup.string().required().min(8),
      name: yup.string().required(),
    });
    return {
      formData: {
        email: "",
        password: "",
        username: "",
        provider: "email",
        token: "",
      },
      password: "",
      visibility: "password",
      value: "",
      LogLoding: false,
      schema,
    };
  },
  components: {
    IgButton,
    Seperator,
    // IgInput,
    // PasswordContainer,
    IgLogo,
    LoadingAuth,
    Form,
    Field,
    ErrorMessage,
  },
  props: {
    ShowLogin: {
      type: Function,
      required: true,
    },
  },
  computed: {
    ...mapState(["isLoggin"]),
  },
  methods: {
    ...mapMutations([
      "USER_LOGGIN_IS_TRUE",
      "TOGGLE_SIGNUP_MODAL",
      "TOGGLE_LOGIN_MODAL",
    ]),
    ...mapActions(["UserRegister"]),

    register() {
      this.UserRegister(this.formData).then(() => {
        this.LogLoding = true;

        setTimeout(() => {
          // hide the loading screen
          this.LogLoding = false;
          // hide the login modal

          // route to home page LogIn
          // this.$router.push("/home");
          // show the RightSideBar
          // this.USER_LOGGIN_IS_TRUE();
          this.ShowLogin();
        }, 4000);
      });
      // this.axios.post(
      //   "http://igame-env.eba-vdb4aabf.us-west-2.elasticbeanstalk.com/api/signup/",
      //   this.formData,
      //   {
      //     headers: { "Content-Type": "application/json", Authorization: "" },
      //   }
      // );

      /// try to storage

      /// end try to storage
      // this.SignUp(this.formData);

      //         this.TOGGLE_LOGIN_MODAL();
      // this.TOGGLE_SIGNUP_MODAL();
    },

    showPaas() {
      this.visibility = "text";
    },
    hidePass() {
      this.visibility = "password";
    },
  },
});
