
import { defineComponent } from "vue";
import { mapMutations } from "vuex";

import Team from "./Team.vue";
import Space from "./Space.vue";
import CoachingService from "./CoachingService.vue";
import SpacePost from "./SpacePost.vue";
import Tournament from "./Tournament.vue";

export default defineComponent({
  name: "AddScreens",
  components: {
    Team,
    Space,
    CoachingService,
    SpacePost,
    Tournament,
  },
  methods: {
    ...mapMutations([
      "TOGGLE_ADD_SCREENS",
      "TOGGLE_TournamentModal_MODAL",
      "TOGGLE_TEAM_MODAL",
      "TOGGLE_Space_MODAL",
      "TOGGLE_SpacePost_MODAL",
      "TOGGLE_CoachingService_MODAL",
    ]),
    toggleAddscreens() {
      this.TOGGLE_ADD_SCREENS();
    },

    toggleTournament() {
      this.TOGGLE_TournamentModal_MODAL();
    },

    toggleTeam() {
      this.TOGGLE_TEAM_MODAL();
      console.log("working");
    },

    toggleSpace() {
      this.TOGGLE_Space_MODAL();
      console.log("working");
    },

    toggleSpacePost() {
      this.TOGGLE_SpacePost_MODAL();
    },

    toggleCoachingService() {
      this.TOGGLE_CoachingService_MODAL();
    },
  },
});
