<template>
  <base-withdraw
    :WithdrawOpen="WithdrawOpen"
    ModalName="withdraw"
    backgroundColor="ig-custome"
    fontBold="ig-bold"
    :closeIcon="require('@/assets/images/close.svg')"
  >
    <div class="ig-mask flex-col m-auto">
      <div class="bg-gray-400 p-30 rounded-xlarge" style="width: 540px">
        <div class="flex flex-col relative">
          <withdraw-form
            v-if="withdrawStep === 1"
            amount="add_amount_for_withdraw"
            addAmount="add_amount"
            addNumber="add_number"
            bankAccountNumber="bank_account_number"
            bankName="bank_name"
            bankHolder="bank_account_holder_name"
            Proceed="proceed"
            addName="add_name"
            :nextpaymentStep="nextpaymentStep"
            :Icon="require('@/assets/images/wallet/dollar-sign.svg')"
          >
            <withdraw-header
              Title="withdraw_balance"
              SelectMethod="provide_info_for_withdraw"
              :closeIcon="require('@/assets/images/close.svg')"
            ></withdraw-header>
          </withdraw-form>
          <withdrawal-success
            v-if="withdrawStep === 2"
            :Icon="require('@/assets/images/wallet/fill-success-img.svg')"
            yourWithdrawSuccess="your_with_draw_success"
            thankYourWithdraw="you_receive_your_with_draw"
            :closeIcon="require('@/assets/images/close.svg')"
            ok="ok"
          ></withdrawal-success>
        </div>
      </div>
    </div>
  </base-withdraw>
</template>

<script lang="ts">
import { mapState } from "vuex";
import baseWithdraw from "@/components/baseUI/WithdrawModal.vue";
import { defineComponent } from "vue";
import WithdrawHeader from "./WithdrawHeader.vue";
import WithdrawalSuccess from "./WithdrawalSuccess.vue";
import WithdrawForm from "./WithdrawForm.vue";

export default defineComponent({
  name: "Withdraw",
  components: {
    baseWithdraw,
    WithdrawHeader,
    WithdrawalSuccess,
    WithdrawForm,
  },
  data: () => ({
    withdrawStep: 1,
    length: 2,
  }),
  computed: {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    ShowStpes(): any {
      return (this.withdrawStep / this.length) * 100;
    },
    ...mapState(["WithdrawOpen"]),
  },
  methods: {
    nextpaymentStep() {
      this.withdrawStep++;
    },
  },
});
</script>
