
import { mapMutations } from "vuex";
import { defineComponent } from "vue";

export default defineComponent({
  methods: {
    ...mapMutations(["TOGGLE_MODAL"]),
    closeModal() {
      this.TOGGLE_MODAL();
    },
  },
  props: {
    Title: {
      type: String,
      required: true,
    },
    SelectMethod: {
      type: String,
      required: true,
    },
    closeIcon: {
      type: String,
      required: true,
    },
  },
});
