<template>
  <div class="bg-black h-full flex-none pt-6">
    <div class="flex justify-center items-center flex-col">
      <button
        class="
          bg-black-100
          w-14
          h-14
          rounded-lg
          flex
          justify-center
          items-center
          focus:outline-none
          transition
          duration-500
          transform
          hover:scale-110
          border border-gray-100
        "
      >
        <svg
          width="16"
          height="16"
          viewBox="0 0 16 16"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            fill-rule="evenodd"
            clip-rule="evenodd"
            d="M11.9576 11.0171L15.8041 14.8637C16.0653 15.1216 16.0653 15.5461 15.8041 15.8041C15.5429 16.0653 15.1216 16.0653 14.8604 15.8041L11.0139 11.9608C9.85143 12.9143 8.36571 13.4857 6.74286 13.4857C3.01714 13.4857 0 10.4653 0 6.74286C0 3.02041 3.01714 0 6.74286 0C10.4653 0 13.4857 3.02041 13.4857 6.74286C13.4857 8.36571 12.911 9.85469 11.9576 11.0171ZM1.33878 6.74286C1.33878 9.72082 3.76163 12.1437 6.74286 12.1437C9.72082 12.1437 12.1469 9.72082 12.1469 6.74286C12.1469 3.7649 9.72082 1.33878 6.74286 1.33878C3.76163 1.33878 1.33878 3.7649 1.33878 6.74286Z"
            fill="white"
          />
        </svg>
      </button>
      <button
        v-for="(item, index) in spaceData"
        :key="index"
        @click="goToMySpacePage(item.id)"
        class="
          focus:outline-none
          mt-6
          mb-6
          transition
          duration-500
          transform
          hover:scale-110
        "
        :style="{ backgroundImage: 'url(' + item.icon + ')' }"
      ></button>
    </div>
  </div>
</template>

<script>
import { defineComponent } from "vue";
import axios from "axios";
import { mapState } from "vuex";
import { emitter } from "@/main";
export default defineComponent({
  data: () => {
    return {
      spaceTotal: 0,
      spacePage: 1,
      spaceData: [],
    };
  },
  computed: {
    ...mapState(["apiUrl"]),
  },
  methods: {
    async getSpaces(page) {
      var token = localStorage.getItem("access_token");
      if (token) {
        try {
          var response = await axios.get(this.apiUrl + "/space", {
            params: {
              page: page,
            },
            headers: {
              authorization: "Bearer " + localStorage.getItem("access_token"),
            },
          });
          if (response.data && response.data.results) {
            this.spacePage = page;
            this.spaceTotal = response.data.count;
            if (page == 1) this.spaceData = [];
            this.spaceData = this.spaceData.concat(response.data.results);
          }
        } catch (error) {
          if (error && error.response) {
            if (error.response.status == 401) {
              localStorage.removeItem("access_token");
              window.location.replace(window.location.origin);
            }
          }
        }
      }
    },
    goToMySpacePage(id) {
      var currentRouteName = this.$router.currentRoute.value.name;
      this.$router
        .push({
          name: "MySpacePage",
          params: { id: id },
        })
        .then(() => {
          if (currentRouteName == "MySpacePage") {
            emitter.emit("changeSaceRouter");
          }
        });
      // this.$router.go(this.$router.currentRoute)
    },
  },
  async created() {
    this.getSpaces(1);
    emitter.on("getSpacesEvent", () => {
      this.getSpaces(1);
    });
  },
  deactivated() {
    emitter.off("getSpacesEvent");
  },
});
</script>
