    paymentMethodIcon: require("@/assets/images/wallet/debit-card.svg"),
  },
  {
    name: "Bank transfer",
    paymentMethodIcon: require("@/assets/images/wallet/bank-transfer.svg"),
  },
  {
    name: "Other payment",
