<template>
  <div
    class="
      absolute
      top-0
      flex-none
      px-8
      pt-8
      h-auto
      min-h-screen
      bg-black
      w-1/45
      border-r-2 border-gray-900
      rounded-bl-xl
      z-50
    "
    style="border-bottom-left: 20px; right: 289px"
  >
    <div class="relative h-full">
      <div class="flex justify-between items-center mb-40">
        <div class="flex items-center">
          <button
            @click="hideChat"
            ml-4
            class="
              border-2 border-transparent
              hover:border-green
              flex
              h-40
              w-40
              justify-center
              items-center
              rounded-full
              transition
              duration-300
              ease-in-out
            "
          >
            <svg
              width="20"
              height="18"
              viewBox="0 0 20 18"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M1.2021 7.57071C1.28393 7.55743 1.36675 7.5513 1.44962 7.55238H15.5592L15.2516 7.40928C14.9508 7.26694 14.6772 7.07322 14.4431 6.83688L10.4864 2.88019C9.96526 2.38275 9.8777 1.58251 10.2789 0.984132C10.7458 0.346492 11.6412 0.208043 12.2789 0.674949C12.3304 0.712691 12.3793 0.753832 12.4254 0.798103L19.5803 7.95305C20.1395 8.51159 20.14 9.41763 19.5814 9.97679C19.5811 9.97715 19.5807 9.97755 19.5803 9.97791L12.4254 17.1329C11.8657 17.6909 10.9597 17.6896 10.4016 17.13C10.3577 17.086 10.3167 17.0391 10.2789 16.9898C9.8777 16.3914 9.96526 15.5911 10.4864 15.0937L14.4359 11.1299C14.6458 10.9197 14.8872 10.7434 15.1514 10.6075L15.5807 10.4144H1.52836C0.797352 10.4415 0.156 9.93086 0.0186691 9.21233C-0.10784 8.43221 0.421986 7.69726 1.2021 7.57071Z"
                fill="white"
              />
            </svg>
          </button>

          <h2 class="text-2xl text-white ml-4">Chats</h2>
        </div>
        <button role="img" @click="toggleAddNewChatsStatus">
          <svg
            width="34"
            height="34"
            viewBox="0 0 34 34"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <rect width="34" height="34" rx="8" fill="#272832" />
            <path
              fill-rule="evenodd"
              clip-rule="evenodd"
              d="M17 12C16.3864 12 15.8889 12.4975 15.8889 13.1111V15.8889H13.1111C12.4975 15.8889 12 16.3864 12 17C12 17.6136 12.4975 18.1111 13.1111 18.1111H15.8889V20.8889C15.8889 21.5025 16.3864 22 17 22C17.6136 22 18.1111 21.5025 18.1111 20.8889V18.1111H20.8889C21.5025 18.1111 22 17.6136 22 17C22 16.3864 21.5025 15.8889 20.8889 15.8889H18.1111V13.1111C18.1111 12.4975 17.6136 12 17 12Z"
              fill="white"
            />
          </svg>
        </button>
      </div>
      <div class="flex">
        <swiper
          class="swiper static"
          :options="swiperOption"
          :slides-per-view="3.5"
          :pagination="{ clickable: true }"
        >
          <swiper-slide>
            <button
              class="h-1/30 w-1/25 text-gray-200"
              :class="
                selectedTab == 'users'
                  ? 'bg-gray-300 font-bold rounded-lg text-sm'
                  : ''
              "
              @click="changeTab('users')"
            >
              Users
            </button>
          </swiper-slide>

          <swiper-slide>
            <button
              class="h-1/30 w-1/25 text-gray-200"
              :class="
                selectedTab == 'groups'
                  ? 'bg-gray-300 font-bold rounded-lg text-sm'
                  : ''
              "
              @click="changeTab('groups')"
            >
              Groups
            </button>
          </swiper-slide>

          <swiper-slide>
            <button
              class="h-1/30 w-1/25 text-gray-200"
              :class="
                selectedTab == 'teamChat'
                  ? 'bg-gray-300 font-bold rounded-lg text-sm'
                  : ''
              "
              @click="changeTab('teamChat')"
            >
              Team Chat
            </button>
          </swiper-slide>

          <swiper-slide>
            <button
              class="h-1/30 w-1/25 text-gray-200"
              :class="
                selectedTab == 'gameChat'
                  ? 'bg-gray-300 font-bold rounded-lg text-sm'
                  : ''
              "
              @click="changeTab('gameChat')"
            >
              Game Chat
            </button>
          </swiper-slide>

          <swiper-slide>
            <button
              class="h-1/30 w-1/25 text-gray-200"
              :class="
                selectedTab == 'tournamentChat'
                  ? 'bg-gray-300 font-bold rounded-lg text-sm'
                  : ''
              "
              @click="changeTab('tournamentChat')"
            >
              Tournament Chat
            </button>
          </swiper-slide>

          <swiper-slide>
            <button
              class="h-1/30 w-1/25 text-gray-200"
              :class="
                selectedTab == 'streamingChat'
                  ? 'bg-gray-300 font-bold rounded-lg text-sm'
                  : ''
              "
              @click="changeTab('streamingChat')"
            >
              Streaming Chat
            </button>
          </swiper-slide>
        </swiper>
      </div>

      <div class="mt-30 mb-30 w-full">
        <input
          placeholder="Search chats"
          class="
            bg-gray-400
            w-full
            h-1/4
            rounded-tr-lg rounded-tl-lg rounded-bl-lg rounded-br-lg
            focus:outline-none
            transition
            duration-700
            ease-in-out
            border border-gray-100
            px-16
            text-white
          "
        />
      </div>
      <div class="text-white" v-if="selectedTab == 'users'">
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>
        <message @click="openMessage"></message>

        <transition name="startMessage">
          <start-message v-if="messagesStatus"></start-message>
        </transition>
      </div>
      <div class="text-white text-2xl" v-if="selectedTab == 'groups'">
        <group-message @click="openGroupMessage"></group-message>
        <group-message></group-message>
        <group-message></group-message>

        <transition name="groupMessage">
          <start-group-messages
            v-if="groupMessageStatus"
          ></start-group-messages>
        </transition>
      </div>

      <div v-if="selectedTab == 'teamChat'">
        <team-chat @click="openTeamChat"></team-chat>
        <team-chat></team-chat>
        <team-chat></team-chat>
        <team-chat></team-chat>

        <transition name="teamChat">
          <start-team-chat v-if="teamChatStatus"> </start-team-chat>
        </transition>
      </div>

      <div v-if="selectedTab == 'gameChat'">
        <game-chat @click="openGameChat"></game-chat>
        <game-chat @click="openGameChat"></game-chat>
        <game-chat @click="openGameChat"></game-chat>

        <transition name="gameChat">
          <start-game-chat v-if="gameChatStatus"></start-game-chat>
        </transition>
      </div>

      <div v-if="selectedTab == 'tournamentChat'">
        <tournament-chat @click="openTournamentChat"></tournament-chat>
        <tournament-chat @click="openTournamentChat"></tournament-chat>
        <tournament-chat @click="openTournamentChat"></tournament-chat>

        <transition name="tournamentChat">
          <start-tournament-chat
            v-if="tournamentChatStatus"
          ></start-tournament-chat>
        </transition>
      </div>

      <div v-if="selectedTab == 'streamingChat'">
        <stream-chat></stream-chat>
      </div>

      <div v-if="addNewChatStatus">
        <add-chat></add-chat>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { Swiper, SwiperSlide } from "swiper/vue";
import Message from "./Message.vue";
import "swiper/components/pagination/pagination.scss";
import SwiperCore, { Pagination } from "swiper";
import StartMessage from "./StartMessage.vue";
import { mapState, mapMutations } from "vuex";
import GroupMessage from "./groups/groupMessage.vue";
import StartGroupMessages from "./groups/StartGroupMessages.vue";
import TeamChat from "./team-chat/TeamChat.vue";
import StartTeamChat from "./team-chat/StartTeamChat.vue";
import GameChat from "./game-chat/GameChat.vue";
import StartGameChat from "./game-chat/StartGameChat.vue";
import TournamentChat from "./tournament-chat/tournamentChat.vue";
import StartTournamentChat from "./tournament-chat/StartTournamentChat.vue";
import StreamChat from "./streaming-chat/StreamChat.vue";
import AddChat from "./add-new-chat/AddNewChat.vue";
SwiperCore.use([Pagination]);

export default defineComponent({
  name: "Chat",
  components: {
    StartMessage,
    SwiperSlide,
    Swiper,
    Message,
    GroupMessage,
    StartGroupMessages,
    TeamChat,
    StartTeamChat,
    GameChat,
    StartGameChat,
    TournamentChat,
    StartTournamentChat,
    StreamChat,
    AddChat,
  },
  data: () => ({
    selectedTab: "users",
    swiperOption: {
      pagination: {
        el: ".swiper-pagination",
        dynamicBullets: true,
        paginationClickable: true,
      },
    },
    chatNames: [
      "Users",
      "Groups",
      "Team chat",
      "Game chat",
      "Tournament chat",
      "Streaming chat",
    ],
  }),
  methods: {
    changeTab(tab: string) {
      this.selectedTab = tab;
    },
    ...mapMutations([
      "TOGGLE_CHAT_MESSAGE",
      "TOGGLE_GROUP_MESSAGE",
      "TOGGLE_TEAM_CHAT",
      "TOGGLE_GAME_CHAT",
      "TOGGLE_TOURNAMENT_CHAT",
      "TOGGLE_CHAT",
      "TOGGLE_ADD_NEW_CAHT",
    ]),
    openMessage() {
      this.TOGGLE_CHAT_MESSAGE();
    },
    openGroupMessage() {
      this.TOGGLE_GROUP_MESSAGE();
    },
    openTeamChat() {
      this.TOGGLE_TEAM_CHAT();
    },
    openGameChat() {
      this.TOGGLE_GAME_CHAT();
    },
    openTournamentChat() {
      this.TOGGLE_TOURNAMENT_CHAT();
    },
    hideChat() {
      this.TOGGLE_CHAT();
    },
    toggleAddNewChatsStatus() {
      this.TOGGLE_ADD_NEW_CAHT();
    },
  },
  computed: {
    ...mapState([
      "messagesStatus",
      "groupMessageStatus",
      "teamChatStatus",
      "gameChatStatus",
      "tournamentChatStatus",
      "streamingChat",
      "addNewChatStatus",
    ]),
  },
});
</script>

<style lang="scss" scoped>
.startMessage,
.groupMessage,
.teamChat,
.gameChat {
  &-enter-from {
    opacity: 0;
  }
  &-enter-to {
    opacity: 1;
  }

  &-leave-active {
    opacity: 0;
  }
  &-enter-active,
  &-leave-active {
    transition: all 0.8s ease-in-out;
  }
}

.addNewChat {
  &-enter-from {
    transform: translate(0, 50px);
  }
  &-enter-to {
    /* transform: translateY(0); */
  }

  &-enter-active,
  &-leave-active {
    transition: all 0.8s ease-in-out;
  }
}
</style>
