
import { mapMutations, mapState } from "vuex";
import { defineComponent } from "vue";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";

export default defineComponent({
  name: "igDialog",
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
  },
  methods: {
    ...mapMutations(["TOGGLE_GROUP_MEMBERS"]),
    showMembers() {
      this.TOGGLE_GROUP_MEMBERS();
    },
  },
  computed: {
    ...mapState(["MembersStatus"]),
  },
});
