
import { mapMutations, mapState } from "vuex";
import { defineComponent } from "vue";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";

export default defineComponent({
  name: "SpacePost",
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
  },
  computed: {
    ...mapState(["SpacePost"]),
  },
  methods: {
    ...mapMutations(["TOGGLE_SpacePost_MODAL"]),
    toggleSpacePost() {
      this.TOGGLE_SpacePost_MODAL();
    },
  },
});
