
import TypeMessage from "../TypeMessage.vue";
import { defineComponent } from "vue";
import { mapMutations } from "vuex";
import Members from "../groups/Members.vue";
export default defineComponent({
  name: "startGameChat",
  components: { TypeMessage, Members },
  methods: {
    ...mapMutations(["TOGGLE_GAME_CHAT"]),
    hideGameChat() {
      this.TOGGLE_GAME_CHAT();
    },
  },
});
