
import DriveMode from "./DriveMode.vue";
import { mapMutations, mapState } from "vuex";
import { defineComponent, ref } from "vue";
import Toggle from "./Toggle.vue";
import JoinOption from "./JoinOption.vue";
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
} from "@headlessui/vue";
import Stepper from "vuejs-stepper";
import {
  Listbox,
  ListboxButton,
  ListboxOptions,
  ListboxOption,
} from "@headlessui/vue";

export default defineComponent({
  name: "CoachingService",
  data: () => ({
    stepperOptions: {
      headers: [
        { title: "Bracket" },
        { title: "Format" },
        { title: "Schedulling" },
      ],

      nextText: "Next",
      prevText: "Previous",
    },
  }),
  setup() {
    const people = [
      { name: "Select" },
      { name: "Arlene Mccoy" },
      { name: "Devon Webb" },
      { name: "Tom Cook" },
      { name: "Tanya Fox" },
      { name: "Hellen Schmidt" },
    ];
    const selectedPerson = ref(people[0]);

    return {
      people,
      selectedPerson,
    };
  },
  components: {
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
    Stepper,
    Listbox,
    ListboxButton,
    ListboxOptions,
    ListboxOption,
    Toggle,
    JoinOption,
    DriveMode,
  },
  computed: {
    ...mapState(["TournamentModal"]),
  },
  methods: {
    ...mapMutations(["TOGGLE_TournamentModal_MODAL"]),
    toggleTournament() {
      this.TOGGLE_TournamentModal_MODAL();
    },
  },
});
