<template>
  <div class="w-full bg-black">
    <Listbox v-model="selectedPerson">
      <div class="relative mt-1">
        <ListboxButton
          class="
            relative
            w-full
            py-2
            pl-3
            pr-10
            text-left
            bg-black
            border border-gray-100
            rounded-lg
            shadow-md
            cursor-default
            focus:outline-none
          "
        >
          <span class="block truncate text-white text-base">{{
            selectedPerson.name
          }}</span>
          <span
            class="
              absolute
              inset-y-0
              right-0
              flex
              items-center
              pr-2
              pointer-events-none
            "
          >
            <svg
              width="10"
              height="11"
              viewBox="0 0 10 11"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M9.96094 3.57666L8.78658 2.40234L4.98141 6.20752L1.17622 2.40234L0.00187302 3.57666L4.98141 8.55619L9.96094 3.57666Z"
                fill="white"
              />
            </svg>
          </span>
        </ListboxButton>

        <transition name="fade">
          <ListboxOptions
            class="
              absolute
              w-full
              py-1
              mt-2
              overflow-hidden
              text-base
              bg-white
              rounded-md
              shadow-lg
              max-h-60
              focus:outline-none
              sm:text-sm
            "
          >
            <ListboxOption
              v-slot="{ active, selected }"
              v-for="person in people"
              :key="person.name"
              :value="person"
              as="template"
            >
              <li
                :class="[
                  active ? 'text-white bg-purple' : 'text-gray-200',
                  'cursor-default select-none relative py-2 pl-10 pr-4',
                ]"
              >
                <span
                  :class="[
                    selected ? 'font-medium' : 'font-normal',
                    'block truncate',
                  ]"
                  >{{ person.name }}</span
                >
                <span
                  v-if="selected"
                  class="
                    absolute
                    inset-y-0
                    left-0
                    flex
                    items-center
                    pl-3
                    text-amber-600
                  "
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="0 0 20 20"
                    fill="currentColor"
                    class="w-5 h-5"
                  >
                    <path
                      fill-rule="evenodd"
                      d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                      clip-rule="evenodd"
                    ></path>
                  </svg>
                </span>
              </li>
            </ListboxOption>
          </ListboxOptions>
        </transition>
      </div>
    </Listbox>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import {
  Listbox,
  ListboxButton,
  ListboxOptions,
  ListboxOption,
} from "@headlessui/vue";
// import { CheckIcon, SelectorIcon } from "@heroicons/vue/solid";

export default defineComponent({
  components: {
    Listbox,

    ListboxButton,
    ListboxOptions,
    ListboxOption,
    // CheckIcon,
    // SelectorIcon,
  },

  setup() {
    const people = [
      { name: "Wade Cooper" },
      { name: "Arlene Mccoy" },
      { name: "Devon Webb" },
      { name: "Tom Cook" },
      { name: "Tanya Fox" },
      { name: "Hellen Schmidt" },
    ];
    const selectedPerson = ref(people[0]);

    return {
      people,
      selectedPerson,
    };
  },
});
</script>

<style lang="scss">
.fade {
  &-enter-from {
    opacity: 0;
    height: 150px;
  }
  &-enter-to {
    opacity: 1;
    height: 230px;
  }
  &-enter-active {
    transition: all 0.4s ease-in-out;
  }
  &-leave-from {
    opacity: 1;
    height: 430px;
  }
  &-leave-to {
    opacity: 0;
    height: 0;
  }
  &-leave-active {
    transition: all 0.4s ease-in-out;
  }
}
</style>
