    name: "Bank transfer",
    paymentMethodIcon: require("@/assets/images/wallet/bank-transfer.svg"),
  },
  {
    name: "Other payment",
    paymentMethodIcon: require("@/assets/images/wallet/other-payment.svg"),
  },
];

export default defineComponent({
  components: {
    RadioGroup,
    RadioGroupLabel,
    RadioGroupDescription,
    RadioGroupOption,
    IgButton,
  },

  setup() {
    const selected = ref("");

    return { selected, plans };
  },

  // data: () => ({
  //   selected: "",
  //   plans,
  // }),

  props: {
    nextpaymentStep: {
      type: Function,
      required: true,
    },
