
import BaseDialog from "@/components/baseUI/IgDialog.vue";
import { defineComponent } from "vue";
import CardHeader from "../screens/CardHeader.vue";
import ChoosePaymentMethod from "../screens/ChoosePaymentMethod.vue";
import InfoCreditCard from "../screens/cards/InfoForCreditCard.vue";
import yourTopUpSuccess from "../screens/Success.vue";
import FillInfoHeader from "../screens/FillInfoHeader.vue";
import FormInputs from "../screens/FormInputs.vue";
import CreditCardBadge from "../screens/CreditCardBadge.vue";
import { mapState } from "vuex";
export default defineComponent({
  name: "TopUpWallet",
  components: {
    BaseDialog,
    CardHeader,
    ChoosePaymentMethod,
    InfoCreditCard,
    yourTopUpSuccess,
    FillInfoHeader,
    FormInputs,
    CreditCardBadge,
  },
  data: () => ({
    paymentStep: 1,
    length: 4,
  }),
  computed: {
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    ShowStpes(): any {
      return (this.paymentStep / this.length) * 100;
    },
    ...mapState(["ModalOpen"]),
  },
  methods: {
    nextpaymentStep() {
      this.paymentStep++;
      console.log("next step working ?");
    },
    previouspaymentStep() {
      this.paymentStep--;
      console.log("previous step wrokign ?");
    },
  },
});
